<template>
  <div class="container">
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item>
              <el-button type="success" @click="createUser">新增</el-button>
              <el-button type="primary" @click="removeAll">批量删除</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table
      :data="tableData"
      class="list"
      v-loading="loading"
      @selection-change="handleSelectionChange"
    >
      >
      <el-table-column type="selection" width="55" />
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="等级名称" align="center" />
      <el-table-column prop="credit" label="积分限制" align="center" />
      <el-table-column prop="apr" label="加息率" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" />
      <el-table-column align="center" label="操作">
        <template slot-scope="scope">
          <el-button size="small" @click="editItem(scope.row.id)" type="primary"
            >编辑</el-button
          >
          <el-button @click="removeItem(scope.row)" type="danger" size="mini"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      count: 0,
      page_curren: 1,
      searchs: {
        keyword_search_value: '',
      },
      tableData: [],
      loading: false,
      selectIds: [],
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    createUser() {
      this.$router.push('/member/level/create')
    },
    removeAll() {
      if (this.selectIds.length < 1) {
        Message.error('未选中数据')
        return
      }
      this.loading = true
      Fetch('/user/level/remove', { id: this.selectIds })
        .then(() => {
          Message.success('删除成功')
          this.FetchList()
          this.loading = false
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    FetchList() {
      this.loading = true
      Fetch('/user/level/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
        })
        .catch(() => (this.loading = false))
    },
    editItem(uid) {
      this.$router.push('/member/level/edit/' + uid)
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        callback: type => {
          if (type === 'confirm') {
            Fetch('/user/level/remove', { id: row.id })
              .then(() => {
                Message.success('删除成功')
                this.FetchList()
              })
              .catch(() => Message.error('删除失败'))
          }
        },
      })
    },
    handleSelectionChange(val) {
      this.selectIds = []
      val.forEach(item => {
        this.selectIds.push(item.id)
      })
    },
  },
}
</script>
